<template>
  <div id="boardlist">
    <h2>
      <router-link id="ADD" to="/add-board">Suggest a new recipe!</router-link>
      <br>
    </h2>

    <div id="search">
      <input type="text" placeholder="Search..." />
      <br>
      <br>
      <h3><u>Categories</u></h3>
      <dl>
        <dt><b>Cats</b></dt>
        <dd>Healthy</dd>
        <dd>Treats</dd>
        <dd>Greens</dd>

        <dt><b>Dogs</b></dt>
        <dd>Healthy</dd>
        <dd>Treats</dd>
        <dd>Greens</dd>
      </dl>
    </div>
    

    <br>
    <div id="show-recipes">
      <div v-for="board in boards" class="single-recipe">
        <h4>{{ board.title }}</h4>
        <img v-bind:src="selectedImage" />
        <router-link v-bind:to="'/show-board/' + board.key">
          <div class="overlay">
            <button class="button" @click="showModal">View Recipe</button>
            <h3>Pet: {{ board.category }}</h3>
            <h3>Diet: {{ board.diet }}</h3>
          </div>
        </router-link>
      </div>
    </div>
  
</div>

</template>

<script>

import firebase from '../Firebase'
import router from '../router'

export default {
  name: 'BoardList',
  data () {
    return {
      fields: {
        actions: { label: 'Action', 'class': 'text-center' },
        title: { label: 'Title', sortable: true, 'class': 'text-center' },
        category: { label: 'Pet', sortable: true, 'class': 'text-center'},
        diet: { label: 'Diet', sortable: true, 'class': 'text-center',}
      },
      boards: [],
      errors: [],
      images: [
      'https://puu.sh/D3p64/baf81755e7.jpg',
    ],
    selectedImage: '',
      ref: firebase.firestore().collection('boards'),
    }
  },
  created () {
    const idx = Math.floor(Math.random() * this.images.length)
    this.selectedImage = this.images[idx]
    this.ref.onSnapshot((querySnapshot) => {
      this.boards = [];
      querySnapshot.forEach((doc) => {
        this.boards.push({
          key: doc.id,
          title: doc.data().title,
          category: doc.data().pet,
          diet: doc.data().ingredients,
        });
      });
    });
  },
  methods: {
    details (board) {
      router.push({ name: 'ShowBoard', params: { id: board.key }})
    }
  }
}
</script>

<style>
  
h4 {
position: absolute;
z-index: 2;
}

#add-recipe{
text-align: center;
}

#search {
position: absolute;
height: auto;
width: auto;
margin-left: 5%;
text-align: left;
}

#search input{
width: auto;
}


.overlay .button {
color: white;
background-color: darkseagreen;
margin-top: 50%;
text-align: left;
z-index: 2;
}

#show-recipes {
max-width: 80%;
margin-left: 20%;
}
  
#show-recipes h4 {
  padding: 30px;
}


.single-recipe {
position: relative;
float: left;
height: 450px;
width: 350px;
padding: 0px;
margin: 20px;
background: #eee;
color: white;
}
  
.overlay {
position: absolute;
bottom: 0;
left: 0;
right: 0;
top: 0;
background-color: black;
overflow: hidden;
height: 0;
transition: .5s ease;
opacity: 0.8;
z-index: 1;
}

img {
position: relative;
width:100%;
height:100%;
object-fit: cover;
overflow: hidden;
bottom: 0;
left: 0;
right: 0;
top: 0;
margin: auto 0px;
z-index: 0;
text-align: center;
}

.btn-info {
margin-top: 40%;
opacity: 100;
}

.single-recipe:hover .overlay{
height: 100%;
bottom: 0;
left: 0;
right: 0;
top: 0;
color: white;
}
  
</style>